                    if(res.data.tickers[0]){
                        const items=res.data.tickers[0].items;
                        let tempData = 0;

                        for (let item of items) {
                            if (item.item === 'grnil3y') {
                                for (let sub of item.days) {
                                    if (sub.values&&sub.values.value) {
                                        tempData+=sub.values.value
                                    }
                                }
                            }
                        }
                        setPercentFirst(tempData*100)
                    }
